import './index.scss'
import { StarRating } from '../../utils/StarRating'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux'
import { deleteReview } from '../../actions/reviewActions'

export default function Reviews() {
    const dispatch = useDispatch()
    const { user, allReviews } = useSelector(state=>({
        user: state.currentUser,
        allReviews: state.review.allReviews
    }));

    return (
        <div className='wrapper'>
            <div className='title'>
                <span>Reviews</span>
            </div>
            <div className='reviews'>
                {allReviews && allReviews.map((review,i)=>
                    <div className="card" key={i}>
                        <div className="card-image">
                            <img src={review.image ? review.image : process.env.PUBLIC_URL + '/images/placeholder-image.png'} alt='Review'/>
                            <span className="card-title">{review.username}</span>
                        </div>
                        <ul className="card-content">
                            <hr/>
                            <li>{review.user}</li>
                            <li>{new Date(parseInt(review.reviewID)).toLocaleDateString('sv')}</li>
                            <li><StarRating max={5} currentNumber={review.rating} /></li>
                            <li>{review.description}</li>
                            <hr/>
                            <li><Link to={`/products/${review.productID.replace(/\s/g,'')}`} >{review.productID}</Link></li>
                            {user && user.admin && (<li><button onClick={(e)=>{e.preventDefault(); dispatch(deleteReview(review.reviewID))} }><small>DELETE</small></button></li>)}
                        </ul>
                    </div> 
                )}
            </div>
        </div>
    )
}
